import React from "react"
import firebase from "gatsby-plugin-firebase"
import localForage from 'localforage';

// import { handleLogin, isLoggedIn } from "../components/auth"

import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import FormControl from 'react-bootstrap/FormControl'
import Button from 'react-bootstrap/Button'
import Alert from 'react-bootstrap/Alert'


const isBrowser = typeof window !== `undefined`;

//let dictinary = localForage.createInstance({ name: "dictionary" });
let userCraft = localForage.createInstance({ name: "userCraft" });


class Login extends React.Component {
  state = {
    email: '',
    password: '',
    message: ''
  }

  // componentWillUnmount() {
  //   firebase.database().off();
  // }

  handleUpdate(event) {
    this.setState({
      [event.target.name]: event.target.value,
    })
  }

  handleRegister() {
    console.log("Registering");
    this.setState({ message: "Registering" });
    firebase.auth().createUserWithEmailAndPassword(this.state.email, this.state.password)
      .then(() => {
        console.log("Registered");
        this.setState({ message: this.state.email + " registered" });
        this.props.setUserCraftLogin(this.state.email);
      })
      .catch(error => {
        //       console.log(":+" + error);
        this.setState({ message: error });
      });;
  }

  handleSubmit(event) {
    event.preventDefault();
    //  handleLogin(this.state);

    firebase.auth().signInWithEmailAndPassword(this.state.email, this.state.password)
      .then(() => {
        let user = this.state.email.replace(/[^a-zA-Z0-9]/g, "_");
        userCraft.setItem("login", this.state.email);
        let allFirebasedataArray = [];
        firebase.database().ref(`usersCraft/${user}`).once('value', function (snap) {
          snap.forEach(function (item) {
            let key = item.key;
            let value = item.val();
            allFirebasedataArray.push({ key, value });
          });
        })
          .then(() => {
            //     console.log(allFirebasedataArray);
            return Promise.all(
              allFirebasedataArray.map(function (item) {
                return userCraft.setItem(item.key, item.value);
              })
            )
              .then(() => {
                // userCraft.getItem("usertasks").then((res) => {
                this.props.setUserCraftLogin(this.state.email);
                //   console.log(res);
                //   this.props.setUserTasks(res  || []);
                // })
              });
          });
      })
      .catch(error => {
        //   console.log(":+" + error);
        this.setState({ message: error });
        this.handleRegister();
      });

  }

  render() {

    if (!isBrowser) return null

    return (
      <div className="mt-5">
        <Form className="m-3">
          <Form.Group as={Row} controlId="login">
            <Form.Label column sm="6">Email</Form.Label>
            <Col sm="6">
              <FormControl
                onChange={e => this.handleUpdate(e)}
                aria-label="email"
                aria-describedby="email"
                type="email"
                name="email"
              />
            </Col>
          </Form.Group>

          <Form.Group as={Row} controlId="formPlaintextPassword">
            <Form.Label column sm="6"> -  Econolabs,   !</Form.Label>
            <Col sm="6">
              <Form.Control
                onChange={e => this.handleUpdate(e)}
                type="password"
                name="password" />
            </Col>
          </Form.Group>
        </Form>
        <Button onClick={(e) => this.handleSubmit(e)} variant="outline-secondary" type="submit" className="mb-5">Log in</Button>
        <Alert variant='light'>{this.state.message}</Alert>
      </div>
    )
  }
}

export default Login

export { userCraft }

